@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@500;700&family=Roboto+Mono:wght@100;300;400;500;700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Raleway:wght@100;200;300;400;500;600;700;800;900&display=swap);
body {
  background-color: #FFF5DA;
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.header{width:100%;display:flex;align-items:center;height:100px}.header a{margin:0 28px;text-decoration:none;color:#181818;font-size:12px;font-weight:600;font-family:'Raleway', sans-serif}.container{position:relative;color:#181818;width:1280px;min-width:1280px;margin:0 auto;display:flex;justify-content:center}.container .page{position:absolute}.container .page .inner{display:flex;justify-content:center;height:90vh}.container .page .inner h1{font-family:'Raleway', sans-serif;font-size:62px;font-weight:800;letter-spacing:0.08rem}.container .page .inner h1 .line-wrap{overflow:hidden;height:70px}.container .page .inner p{font-family:'Roboto Mono', monospace;margin-top:200px;width:340px;font-weight:300;line-height:26px;font-size:14px}.container .page .inner button{margin-top:20px;width:170px;height:50px;background:none;font-family:'Roboto Mono', monospace;font-size:14px;cursor:pointer}.container .page .inner button:hover{background-color:#1b1b1c;color:white;height:55px}.container .page .inner .black{background-color:#1b1b1c;color:white}.container .page .inner .black:hover{background-color:white;color:#1b1b1c}.page-enter{opacity:0}.page-enter-active{opacity:1;transition:opacity 400ms;transition-delay:600ms}.page-exit{opacity:1}.page-exit-active{opacity:0;transition:opacity 400ms}

.projects p{font-weight:300;line-height:26px;font-size:14px}.projects .title{padding:20px;margin-bottom:90px;display:flex;flex-direction:column;font-family:'Raleway', sans-serif}.projects .title h2{font-size:45px}.projects .grid{display:grid;background-color:red;color:#1b1b1c;padding:10px;grid-template-columns:repeat(3, 1fr);grid-template-rows:repeat(2, 1fr);grid-gap:10px;gap:10px;justify-content:center;width:90vw;font-family:'Roboto Mono', monospace}.projects .grid .project{background-color:white;padding:10px}.projects .grid .project img:hover{transform:translate(0px, 8px);transition-duration:.8s}.projects .grid .project .image{height:50%;width:100%;object-fit:cover}.projects button{width:290px;height:50px;background:none;font-family:'Roboto Mono', monospace;font-size:16px;cursor:pointer}.projects button:hover{background-color:#1b1b1c;color:white;height:55px}.visible,.invisible{opacity:0.0;transition:opacity 0.5s ease}.visible{opacity:1.0}

